export default class Errors {

  httpTranslate (errorcode) {
    switch (errorcode) {
      case 400:
        return 'No  possvel localizar a pgina da Web'
      case 401:
        return 'Acesso negado'
      case 403:
        return 'Acesso proibido.'
      case 404:
        return 'Pgina no encontrada.'
      case 405:
        return 'O verbo HTTP usado para acessar esta pgina no  permitido'
      case 406:
        return 'O navegador do cliente no aceita o tipo da pgina solicitada'
      case 407:
        return 'Autenticao proxy solicitada'
      case 408:
        return 'O site est ocupado demais para mostrar a pgina da Web'
      case 409:
        return 'O site est ocupado demais para mostrar a pgina da Web'
      case 410:
        return 'A pgina da Web no existe mais'
      case 412:
        return 'Erro de precondicionamento'
      case 413:
        return 'Entidade solicitada muito grande'
      case 414:
        return 'URI solicitada muito grande'
      case 415:
        return 'Tipo de mdia sem suporte'
      case 416:
        return 'Intervalo solicitado no satisfatrio'
      case 417:
        return 'Erro na execuo'
      case 423:
        return 'Erro no bloqueio'
      case 500:
        return 'Erro interno de servidor'
      case 501:
        return 'Os valores de cabealho especificam uma configurao que no est implementada'
      case 502:
        return 'O servidor recebeu uma resposta invlida ao atuar como gateway ou proxy'
      case 503:
        return 'Servio indisponvel'
      case 504:
        return 'Tempo limite de gateway'
      case 505:
        return 'Verso HTTP no suportada'
      default:
        return 'Erro ao carregar a pgina'
    }
  }

}



